<template>
  <div class="integdetail-container">

    <div class="tab-bar">
      <div class="tab-item" v-bind:class="{'selected': isselected == 0}" @click="tabSelected(0)">收入</div>
      <div class="tab-item" v-bind:class="{'selected': isselected == 1}" @click="tabSelected(1)">支出</div>
    </div>

      <div class="list-container" v-show="isselected == 0" >
        <div class="list-item" v-for="item in income" :key="item.index">
          <div class="list-left">
            <div class="list-text">{{item.source}}：{{item.way}}</div>
            <div class="list-date">{{item.date}}</div>
          </div>
          <div class="list-right" style="color: red">+{{item.val}}</div>
        </div>
      </div>

      <div class="list-container" v-show="isselected == 1">
        <div class="list-item" v-for="item in expend" :key="item.index">
          <div class="list-left">
            <div class="list-text">{{item.source}}：{{item.way}}</div>
            <div class="list-date">{{item.date}}</div>
          </div>
          <div class="list-right" style="color: green">-{{item.val}}</div>
        </div>
      </div>
  
  </div>
</template>

<script>
import card from '@/components/card'

export default {
  data () {
    return {
      userInfo: {},
      isselected: 0,
      income: [{
        'source': '每日签到',
        'way': '移动端签到',
        'val': 3,
        'date': '2018-03-22'
      }, {
        'source': '每日签到',
        'way': '移动端签到',
        'val': 3,
        'date': '2018-03-22'
      }, {
        'source': '每日签到',
        'way': '移动端签到',
        'val': 3,
        'date': '2018-03-22'
      }],
      expend: [{
        'source': '购买商品',
        'way': '电动剃须刀',
        'val': 100,
        'date': '2018-03-22'
      }, {
        'source': '购买商品',
        'way': '电动剃须刀',
        'val': 100,
        'date': '2018-03-22'
      }, {
        'source': '购买商品',
        'way': '电动剃须刀',
        'val': 100,
        'date': '2018-03-22'
      }]
    }
  },

  components: {
    card
  },

  methods: {
    tabSelected (e) {
      this.isselected = e
    },

    bindviewTap (e) {
      // wx.request({
      //   url: 'test.php', // 仅为示例，并非真实的接口地址
      //   data: {
      //     x: '',
      //     y: ''
      //   },
      //   header: {
      //     'content-type': 'application/json' // 默认值
      //   },
      //   success (res) {
      //     console.log(res.data)
      //   }
      // })
    },
    // getUserInfo () {
    //   // 调用登录接口
    //   wx.login({
    //     success: () => {
    //       wx.getUserInfo({
    //         success: (res) => {
    //           this.userInfo = res.userInfo
    //         }
    //       })
    //     }
    //   })
    // },
    clickHandle (msg, ev) {
      console.log('clickHandle:', msg, ev)
    }
  },

  created () {
    // 调用应用实例的方法获取全局数据
    // this.getUserInfo()
  }
}
</script>

<style scoped>
.integdetail-container {
  height: 100%;
  width: 750rpx;
  background-color: #e6e6e6;
}

.tab-bar {
  position: relative;
  height: 90rpx;
  width: 100%;
  font-size: 15px;
  background-color: #fafafa;
  display: flex;
  align-items: center;
}

.tab-item {
  position: relative;
  display: flex;
  flex: 1;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.selected {
  color: red;
  border-bottom: 2px solid red;
}

.list-container {
  position: relative;
  width: 100%;
  font-size: 15px;
  margin-top: 15rpx 0;
  background: white;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
}

.list-item {
  height: 60px;
  display: flex;
  align-items: center;
  position: relative;
}

.list-item::after {
  content: '';
  position: absolute;
  top: 0;
  left: 30rpx;
  width: 100%;
  height: 1px;
  background: #e6e6e6;
}

.list-date {
  font-size: 12px;
  color: #666666;
}
.list-item-right {
  position: absolute;
  left: 30%;
}

.list-left {
  position: absolute;
  left:20rpx;
}

.list-right {
  position: absolute;
  right:20rpx;
}
</style>



